"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useContext } from "react";

import Link from "next/link";

const Registration = () => {
  //Form variabels
  const [name, setName] = useState<string>("");
  const [surname, setSurname] = useState<string>("");
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  //Response variabels
  const [userDataDeliverd, setUserDataDeliverd] = useState<string | null>(null);
  const [responseError, setResponseError] = useState<string | null>(null);
  const [pending, setPending] = useState<boolean>(false);
  //Input errors
  const [errorName, setErrorName] = useState<string | null>(null);
  const [errorSurname, setErrorSurname] = useState<string | null>(null);
  const [errorEmail, setErrorEmail] = useState<string | null>(null);
  const [errorPassword, setErrorPassword] = useState<string | null>(null);

  const router = useRouter();
  const API_URL = process.env.NEXT_PUBLIC_API_URL!;

  const submitForm = async (e: React.FormEvent) => {
    e.preventDefault();

    let isValid = true;

    if (!name) {
      setErrorName("Musíte zadať meno");
      isValid = false;
    } else {
      setErrorName(null);
    }

    if (!surname) {
      setErrorSurname("Musíte zadať priezvisko");
      isValid = false;
    } else {
      setErrorSurname(null);
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!email || !emailRegex.test(email)) {
      setErrorEmail("Musíte zadať platnú e-mailovú adresu");
      isValid = false;
    } else {
      setErrorEmail(null);
    }

    if (!password || password.length < 6) {
      setErrorPassword("Heslo musí mať aspoň 6 znakov");
      isValid = false;
    } else {
      setErrorPassword(null);
    }

    if (!isValid) return;

    setPending(true);
    const data = { name, surname, email, password };

    try {
      const response = await fetch(`${API_URL}/registration`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
        credentials: "include",
      });

      const result = await response.json();
      console.log(result);
      if (!response.ok) {
        if (result === "Použivateľ už existuje") {
          setResponseError(result);
        }
        setPending(false);
      } else {
        setPending(false);

        router.push("/prihlasenie");
      }
    } catch (error: any) {
      console.error("Chyba pri registrácii:", error.message);
    }
  };

  return (
    <section className="flex-center h-[70vh]">
      <form onSubmit={submitForm} className="form">
        <h2 className="text-black text-center mb-5">Registrácia</h2>
        {responseError && (
          <span className="flex-center">
            <p className="text-center text-red-500 font-bold ">
              {responseError}
            </p>
            <Link
              href={"prihlasenie"}
              className=" underline hover:bg-transparent text-dark-purple m-0 font-medium text-[15px]"
            >
              Prihlásiť sa
            </Link>
          </span>
        )}
        {!pending && (
          <>
            <div className="flex items-center gap-2 w-full">
              <div className="flex flex-col w-full">
                <label>Meno</label>
                <input
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
                {errorName && <p className="errorRegistration">{errorName}</p>}
              </div>
              <div className="flex flex-col w-full">
                <label>Priezvisko</label>
                <input
                  type="text"
                  value={surname}
                  onChange={(e) => setSurname(e.target.value)}
                />
                {errorSurname && (
                  <p className="errorRegistration">{errorSurname}</p>
                )}
              </div>
            </div>

            <label>Email:</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            {errorEmail && <p className="errorRegistration">{errorEmail}</p>}

            <label>Heslo:</label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            {errorPassword && (
              <p className="errorRegistration">{errorPassword}</p>
            )}

            <button type="submit" className="buttonForm">
              Potvrdiť
            </button>
          </>
        )}

        {pending && (
          <p className="font-bold text-dark-purple text-center">
            Kontroluje sa ...
          </p>
        )}
      </form>
    </section>
  );
};

export default Registration;
